import React, { useState, useEffect, useRef } from 'react';
import * as Tone from 'tone';
import './resourcesPage.css';

export default function ResourcesPage() {
  const [bpm, setBpm] = useState(120);
  const [isPlaying, setIsPlaying] = useState(false);
  const [note, setNote] = useState('A4');
  const [isTuning, setIsTuning] = useState(false);
  const [isTimerRunning, setIsTimerRunning] = useState(false);
  const [timeLeft, setTimeLeft] = useState(300); // Default 5 minutes
  const [totalTime, setTotalTime] = useState(300);
  const beatIndicator = useRef(null);
  const timerInterval = useRef(null);

  // Metronome Effect
  useEffect(() => {
    Tone.Transport.bpm.value = bpm;
    return () => {
      Tone.Transport.stop();
    };
  }, [bpm]);

  const startMetronome = () => {
    const synth = new Tone.Synth().toDestination();
    Tone.Transport.scheduleRepeat((time) => {
      synth.triggerAttackRelease('C4', '8n', time);
      if (beatIndicator.current) {
        beatIndicator.current.classList.add('beat-active');
        setTimeout(() => {
          beatIndicator.current.classList.remove('beat-active');
        }, 100);
      }
    }, '4n');

    Tone.start();
    Tone.Transport.start();
    setIsPlaying(true);
  };

  const stopMetronome = () => {
    Tone.Transport.stop();
    setIsPlaying(false);
  };

  const toggleMetronome = () => {
    isPlaying ? stopMetronome() : startMetronome();
  };

  const toggleTuner = () => {
    setIsTuning(!isTuning);
  };

  // Timer Functionality
  const startTimer = () => {
    if (isTimerRunning) return;

    setIsTimerRunning(true);
    timerInterval.current = setInterval(() => {
      setTimeLeft((prev) => {
        if (prev <= 1) {
          clearInterval(timerInterval.current);
          setIsTimerRunning(false);
          alert('Practice session completed!');
          return totalTime;
        }
        return prev - 1;
      });
    }, 1000);
  };

  const stopTimer = () => {
    clearInterval(timerInterval.current);
    setIsTimerRunning(false);
  };

  const handleTimeChange = (e) => {
    const minutes = Number(e.target.value);
    const newTime = minutes * 60;
    setTotalTime(newTime);
    setTimeLeft(newTime);
  };

  const getFormattedTime = () => {
    const minutes = Math.floor(timeLeft / 60);
    const seconds = timeLeft % 60;
    return `${minutes}:${seconds < 10 ? '0' : ''}${seconds}`;
  };

  return (
    <div className="resources-container">
      <h1>Resources</h1>
      <div className="resources-grid">
        {/* Metronome Card */}
        <div className="metronome-card">
          <h2>Visual Metronome</h2>
          <div ref={beatIndicator} className="beat-indicator"></div>
          <input
            type="range"
            min="40"
            max="240"
            value={bpm}
            onChange={(e) => setBpm(Number(e.target.value))}
            className="bpm-slider"
          />
          <span className="bpm-label">BPM: {bpm}</span>
          <button className="metronome-button" onClick={toggleMetronome}>
            {isPlaying ? 'Stop Metronome' : 'Start Metronome'}
          </button>
        </div>

        {/* Tuner Card */}
        <div className="tuner-card">
          <h2>Instrument Tuner</h2>
          <div className="tuner-display">
            <span>Note: {note}</span>
          </div>
          <button className="tuner-button" onClick={toggleTuner}>
            {isTuning ? 'Stop Tuner' : 'Start Tuner'}
          </button>
        </div>

        {/* Practice Timer Card */}
        <div className="timer-card">
          <h2>Music Practice Timer</h2>
          <div className="timer-display">{getFormattedTime()}</div>
          <input
            type="number"
            min="1"
            max="60"
            placeholder="Set time (minutes)"
            onChange={handleTimeChange}
            className="time-input"
          />
          <button className="timer-button" onClick={isTimerRunning ? stopTimer : startTimer}>
            {isTimerRunning ? 'Stop Timer' : 'Start Timer'}
          </button>
          <div className="progress-bar">
            <div
              className="progress"
              style={{ width: `${(1 - timeLeft / totalTime) * 100}%` }}
            ></div>
          </div>
        </div>
      </div>
    </div>
  );
}
