// cookieUtil.js

// Function to retrieve a cookie by name
export function getCookie(name) {
    const value = `; ${document.cookie}`;
    const parts = value.split(`; ${name}=`);
    if (parts.length === 2) return parts.pop().split(";").shift();
    return undefined;
}

// Function to set a cookie with a specified expiration time (in hours)
export function setCookie(cname, cvalue, exphours) {
    const expiration = new Date();
    expiration.setTime(expiration.getTime() + exphours * 60 * 60 * 1000);
    const expires = "expires=" + expiration.toUTCString();
    document.cookie = `${cname}=${cvalue}; ${expires}; path=/`;
}

// Function to check if a cookie is set by name
export function isCookieSet(name) {
    return document.cookie.includes(`${name}=`);
}

// Function to delete a cookie by name
export function deleteCookie(name) {
    document.cookie = `${name}=; expires=Thu, 01 Jan 1970 00:00:00 UTC; path=/;`;
}
