import "./loginPage.css";
import { useRef, useState } from "react";
import { GoogleLogin } from '@react-oauth/google';
import { Link, useNavigate } from 'react-router-dom';
import Logo from "./Logo";
import { setCookie } from "./cookieUtil";


export default function LoginPage() {
    const [remember, setRemember] = useState(false);
    const [errorMessage, setErrorMessage] = useState("");
    const navigate = useNavigate();

    const uname = useRef();
    const pwd = useRef();

    // Function to handle successful login
    const handleLoginSuccess = (data) => {
        const expiration = remember ? 72 : 1;
        setCookie("token", data.token, expiration);
        setCookie("studentId", data.studentId, expiration);
        setCookie("teacherId", data.teacherId, expiration);

        console.log("student ID 2: ", data.studentId);
        console.log("teacher ID 2: ", data.teacherId);

        navigate("/calendar");
    };



    // Traditional login with email and password
    const login = () => {
        fetch("http://localhost:3001/login", {
            method: "POST",
            headers: { "Content-Type": "application/json" },
            body: JSON.stringify({
                email: uname.current.value,
                pwd: pwd.current.value
            }),
        })
        .then((response) => {
            if (!response.ok) {
                if (response.status === 401) {
                    pwd.current.value = "";
                    throw new Error("Invalid credentials, please try again.");
                } else {
                    throw new Error("Error connecting to server.");
                }
            }
            return response.json();

        })
        .then(handleLoginSuccess)
        .catch((error) => setErrorMessage(error.message));
    };

    // Google Login success handler
    const handleGoogleSuccess = (response) => {
        const credential = response.credential;

        fetch("http://localhost:3001/google-login", {
            method: "POST",
            headers: { "Content-Type": "application/json" },
            body: JSON.stringify({ token: credential }),
        })
        .then(response => {
            if (!response.ok) throw new Error("Google sign-in failed.");
            return response.json();
        })
        .then((data) => {
            if (data.userNotRegistered) {
                setCookie("credential", credential, 0.25)
                navigate("/googleRegister")
            } else {
                handleLoginSuccess(data)
            }
        })
        .catch(error => {
            console.error(error)
            setErrorMessage("Error during Google sign-in.")
        });
    };

    return (
        <div className="login-form">
            <Logo />
            <div className="container">
                <label htmlFor="uname"><b>Username</b></label>
                <input ref={uname} type="text" placeholder="Enter Username" name="uname" required />

                <label htmlFor="psw"><b>Password</b></label>
                <input ref={pwd} type="password" placeholder="Enter Password" name="psw" required />

                <button onClick={login}>Login</button>
                <label>
                    Remember me
                    <input type="checkbox" checked={remember} onChange={() => setRemember(!remember)} />
                </label>

                <p style={{ color: "darkred" }}>{errorMessage}</p>
            </div>

            <div className="container">
                <GoogleLogin
                    onSuccess={handleGoogleSuccess}
                    onError={() => console.log("Google Sign-In Failed")}
                    useOneTap
                />
            </div>

            <div className="container">
                Don't have an account? <Link to={"/register"}>Sign up here!</Link>
            </div>

            <div className="container">
                <button
                    type="button"
                    className="cancelbtn"
                    onClick={() => window.history.go(-3)}
                >
                    Cancel
                </button>
                <span className="psw"><Link to="/">Forgot password?</Link></span>
            </div>
        </div>
    );
}
