import React, {useCallback, useEffect, useState} from 'react';
import FullCalendar from '@fullcalendar/react';
import dayGridPlugin from '@fullcalendar/daygrid';
import timeGridPlugin from '@fullcalendar/timegrid';
import interactionPlugin from '@fullcalendar/interaction';
import { getCookie, isCookieSet } from './cookieUtil.js';
import { useNavigate } from 'react-router-dom';
import './calendarPage.css';
import { getRole } from './authUtil.js';

export default function CalendarPage() {
  const [events, setEvents] = useState([]);
  const [showModal, setShowModal] = useState(false);
  const [lessonDate, setLessonDate] = useState('');
  const [lessonDuration, setLessonDuration] = useState('');
  const [lessonNotes, setLessonNotes] = useState('');
  const [lessonEmail, setLessonEmail] = useState('');
  const [role, setRole] = useState(undefined);
  const [students, setStudents] = useState([]);
  const [studentsOfTeacher, setStudentsOfTeacher] = useState([])
  const [selectedStudents, setSelectedStudents] = useState([])

  const navigate = useNavigate();
  const [lessonId, setLessonId] = useState(null);
  const [isEditMode, setIsEditMode] = useState(false);
  const [lessonTitle, setLessonTitle] = useState('');

  const fetchStudentNames = useCallback(async (studentIds) => {
    console.log("Called Fetch")
    const studentNames = await Promise.all(studentIds.map(async (id) => {
      try {
        const response = await fetch(`http://localhost:3001/students/${id}`);
        if (!response.ok) {
          console.error(`Failed to fetch student with ID ${id}: ${response.status}`);
          return id; // Fallback to ID if name is not found
        }
        const student = await response.json();
        return student?.name || id;
      } catch (error) {
        console.error(`Error fetching student with ID ${id}:`, error);
        return id;
      }
    }));
    return studentNames;
}, [])

  const fetchLessons = useCallback(async () => {
    const studentId = getCookie("studentId");
    const teacherId = getCookie("teacherId");
  
    try {
      const userId = role === "teacher" ? teacherId : studentId;
      const userRoleKey = role === "teacher" ? "teacherId" : "studentId";
      const response = await fetch(`http://localhost:3001/lessons?${userRoleKey}=${userId}`);

      const data = await response.json();
      


      if (Array.isArray(data)) {
          const formattedEvents = await Promise.all(data.map(async (lesson) => {

              const studentNames = await fetchStudentNames(lesson.studentIds || []);

              return {
                  id: lesson._id,
                  title: lesson.title || 'Lesson',
                  start: lesson.date,
                  end: new Date(new Date(lesson.date).getTime() + lesson.duration * 60000).toISOString(),
                  description: lesson.notes || 'No description',
                  students: studentNames,
              };
          }));

          setEvents(formattedEvents);
      } else {
          console.error("Invalid response format:", data);
      }
  } catch (error) {
      console.error("Error fetching lessons:", error);
    }
  }, [role, fetchStudentNames])

  useEffect(() => {
    if (!isCookieSet("token")) {
      navigate("/login");
    }

    getRole().then(userRole => {
      if (!userRole) {
        navigate("/login");
      } else {
        setRole(userRole);
        if (userRole === "teacher") {
          fetch(`http://localhost:3001/teachers/students?token=${getCookie("token")}`)
              .then(res => {
                if (res.ok) {
                  res.json().then(students => {
                    setStudentsOfTeacher(students)
                  })
                } else if (res.status === 401) {
                  navigate("/login")
                }
              })
        }
        fetchLessons();
      }
    });
  }, [navigate, fetchLessons]);

  const handleAddOrEditLessonSubmit = async (e) => {
    e.preventDefault();

    if (role !== "teacher") {
        console.error("Only teachers may schedule or edit lessons");
        return;
    }

    try {
        // Split the input into an array of emails and filter out empty entries
        const emailArray = lessonEmail.split(',').map(email => email.trim()).filter(email => email !== '');

        // Initialize studentIds with existing student IDs
        const studentIds = [...students.map(student => student.id)];

        // Fetch student IDs for each email
        for (const email of emailArray) {
            try {
                const userResponse = await fetch(`http://localhost:3001/lessons/findByEmail?email=${email}`);

                if (!userResponse.ok) {
                    console.error(`User not found with email: ${email}`);
                    continue;
                }

                const responseData = await userResponse.json();

                const { userId } = responseData;
                if (userId && !studentIds.includes(userId)) {
                    studentIds.push(userId);
                }
            } catch (error) {
                console.error(`Error fetching student ID for email: ${email}`, error);
            }
        }

        selectedStudents.forEach(studentId => studentIds.push(studentId))



        // Prepare lesson data for submission
        const lessonData = {
            studentIds,
            teacherIds: role === "teacher" ? [getCookie("teacherId")] : [],
            title: lessonTitle,
            date: lessonDate,
            duration: lessonDuration,
            notes: lessonNotes,
        };

        const method = isEditMode ? "PUT" : "POST";
        const url = isEditMode ? `http://localhost:3001/lessons/${lessonId}` : `http://localhost:3001/lessons`;

        // Make the API request to create/update the lesson
        const response = await fetch(url, {
            method,
            headers: {
                "Content-Type": "application/json",
            },
            body: JSON.stringify(lessonData),
        });


        if (!response.ok) {
            const errorText = await response.text();
            console.error(`Error creating/updating lesson: ${response.statusText}`, errorText);
            throw new Error(`Error creating/updating lesson: ${response.statusText}`);
        }


        // Fetch the updated lessons list to refresh the state
        await fetchLessons();

        closeModal();
    } catch (error) {
        console.error("Error submitting lesson:", error);
    }
};

  
  

  const handleDeleteLesson = async () => {
    if (!lessonId || role !== "teacher") return;

    try {
      const response = await fetch(`http://localhost:3001/lessons/${lessonId}`, {
        method: 'DELETE',
      });

      if (!response.ok) {
        throw new Error(`Error deleting lesson: ${response.statusText}`);
      }

      fetchLessons();
      closeModal();
    } catch (error) {
      console.error("Error deleting lesson:", error);
    }
  };

  const handleEventClick = (clickInfo) => {
    const event = clickInfo.event;
    setLessonTitle(event.title);
    setLessonId(event.id);
    
    const localDate = new Date(event.start.getTime() - event.start.getTimezoneOffset() * 60000)
      .toISOString()
      .slice(0, 16);
    setLessonDate(localDate);

    setLessonDuration((new Date(event.end) - new Date(event.start)) / 60000);
    setLessonNotes(event.extendedProps.description || '');
    setStudents(event.extendedProps.students || []); // Ensure names are being set here

    const studentNames = (event.extendedProps.students || []).join(', ');
    setLessonEmail(studentNames);
    
    setIsEditMode(true);
    setShowModal(true);
  };
  

  const openModalForNewLesson = () => {
    if (role !== "teacher") return;

    setIsEditMode(false);
    setLessonId(null);
    setLessonDate('');
    setLessonDuration('');
    setLessonNotes('');
    setLessonTitle('');
    setLessonEmail('');
    setShowModal(true);
  };

  const closeModal = () => {
    setShowModal(false);
    setLessonDate('');
    setLessonDuration('');
    setLessonNotes('');
    setLessonTitle('');
    setLessonEmail('');
  };

  return (
    <div className="calendar-container">
      <h1>Your Lessons</h1>

      {role === "teacher" && (
        <button className="add-lesson-btn" onClick={openModalForNewLesson}>
          + Schedule Lesson
        </button>
      )}

      <FullCalendar
        plugins={[dayGridPlugin, timeGridPlugin, interactionPlugin]}
        initialView="dayGridMonth"
        headerToolbar={{
          left: 'prev,next today',
          center: 'title',
          right: 'dayGridMonth,timeGridWeek,timeGridDay'
        }}
        events={events}
        eventClick={handleEventClick}
        editable={role === "teacher"}
        selectable={role === "teacher"}
        eventTimeFormat={{
          hour: '2-digit',
          minute: '2-digit',
          hour12: false
        }}
      />

      {showModal && (
        <div className="modal">
          <div className="modal-content">
            <span className="modal-close" onClick={closeModal}>&times;</span>
            <h2>Lesson Details</h2>
            <form onSubmit={handleAddOrEditLessonSubmit}>
              <label>Title</label>
              <input
                type="text"
                value={lessonTitle}
                onChange={(e) => setLessonTitle(e.target.value)}
                required
                readOnly={role !== "teacher"}
              />

              {role === "teacher" ? (
                <>
                  <label>Select students</label>
                  <div className={"select"}>
                    {studentsOfTeacher.sort((a, b) => a.name.localeCompare(b.name)).map(student =>
                      // <option value={student._id}>
                      //   {student.name} ({student.email})
                      // </option>
                        <><label><input onChange={e => {
                          if (e.target.checked) {
                            selectedStudents.push(e.target.value)
                          } else {
                            selectedStudents.splice(selectedStudents.indexOf(e.target.value), 1)
                          }
                          console.log(selectedStudents)
                        }} type="checkbox" value={student._id}/>{student.name} ({student.email})</label><br/></>
                    )}
                  </div>

                  <label>Add additional attendees</label>
                  <textarea
                    value={lessonEmail}
                    onChange={(e) => setLessonEmail(e.target.value)}
                    placeholder="Enter emails separated by commas"
                  />

                </>
              ) : (
                <div>
                  <label>Students</label>
                  <ul>
                    {students.map(student => (
                      <li key={student}>{student}</li>
                    ))}
                  </ul>
                </div>
              )}

              <label>Date</label>
              <input
                type="datetime-local"
                value={lessonDate}
                onChange={(e) => setLessonDate(e.target.value)}
                required
                readOnly={role !== "teacher"}
              />

              <label>Duration (in minutes)</label>
              <input
                type="number"
                value={lessonDuration}
                onChange={(e) => setLessonDuration(e.target.value)}
                min={0}
                step={5}
                required
                readOnly={role !== "teacher"}
              />

              <label>Notes</label>
              <textarea
                value={lessonNotes}
                onChange={(e) => setLessonNotes(e.target.value)}
                readOnly={role !== "teacher"}
              />

              {role === "teacher" && (
                <>
                  <button type="submit" className="save-btn">Save</button>
                  {isEditMode && (
                    <button type="button" className="delete-btn" onClick={handleDeleteLesson}>
                      Remove Lesson
                    </button>
                  )}
                </>
              )}

            </form>
          </div>
        </div>
      )}
    </div>
  );
}