import "./profilePage.css";
import { useEffect, useState, useRef } from "react";
import { useNavigate } from "react-router-dom";
import { deleteCookie, getCookie } from "./cookieUtil";
import { getRole } from "./authUtil";

export default function ProfilePage() {
    const [user, setUser] = useState({});
    const [role, setRole] = useState(undefined);
    const newStudentInput = useRef();
    const [students, setStudents] = useState([]);
    const [isModalOpen, setIsModalOpen] = useState(false);
    const navigate = useNavigate();

    useEffect(() => {
        const token = getCookie("token");

        // If no token is present, redirect to login
        if (!token) {
            navigate("/login");
            return;
        }

        // Get user role
        getRole().then((userRole) => {
            if (userRole === undefined) {
                navigate("/login");
            } else {
                setRole(userRole);
            }
        });

        if (role === "teacher") {
            fetch(`http://localhost:3001/teachers?token=${token}`)
                .then((res) => {
                    if (!res.ok) {
                        if (res.status === 401) {
                            navigate("/login");
                        } else {
                            throw new Error(`Response status: ${res.status}`);
                        }
                    } else {
                        return res.json();
                    }
                })
                .then((teacher) => {
                    setStudents(teacher.studentIds);
                });
        }

        // Fetch user data from the API
        const fetchUserData = async () => {
            try {
                const response = await fetch(`http://localhost:3001/user?token=${token}`);
                if (!response.ok) {
                    if (response.status === 401) {
                        navigate("/login");
                    } else {
                        throw new Error(`Response status: ${response.status}`);
                    }
                } else {
                    const userData = await response.json();
                    setUser(userData);
                }
            } catch (err) {
                console.error("Error fetching user data:", err);
            }
        };

        fetchUserData();
    }, [navigate, role]); // Removed students from dependencies

    const handleLogout = () => {
        deleteCookie("token");
        navigate("/login");
    };

    const addStudent = (email) => {
    const token = getCookie("token");
    fetch(
        `http://localhost:3001/teachers/addStudent?token=${token}&email=${email}`,
        { method: "POST" }
    )
    .then(res => {
        if (res.ok) {
            newStudentInput.current.value = ""; // Clear the input field
            return fetch(`http://localhost:3001/teachers/students?token=${token}`); // Fetch updated students
        } else {
            if (res.status === 401) {
                // Redirect to login if unauthenticated
                navigate("/login");
            } else if (res.status === 404) {
                console.error("Student not found");
            } else {
                throw new Error(`Response status: ${res.status}`);
            }
        }
    })
    .then(res => res.json())
    .then(updatedStudents => {
        setStudents(updatedStudents); // Update the students in real-time
    })
    .catch(err => {
        console.error("Error adding student or fetching updated list:", err);
    });
};


    const removeStudent = (email) => {
        const token = getCookie("token");
        fetch(
            `http://localhost:3001/teachers/removeStudent?token=${token}&email=${email}`,
            { method: "POST" }
        )
        .then(res => {
            if (res.ok) {
                // Update the state to reflect the changes
                setStudents(students.filter(student => student.email !== email));
            } else {
                if (res.status === 401) {
                    navigate("/login");
                } else if (res.status === 404) {
                    alert("Student not found.");
                } else {
                    throw new Error(`Response status: ${res.status}`);
                }
            }
        })
        .catch(err => console.error("Error removing student:", err));
    };
    
    
    

    const openModal = () => setIsModalOpen(true);
    const closeModal = () => setIsModalOpen(false);

    return (
        <>
            {user.email ? (
                <div className="profile-container">
                    <h1>{user.name}</h1>
                    <p>Email on record: {user.email}</p>

                    {role === "teacher" && (
                        <>
                            <div className="students-section">
                                <h2>Students</h2>
                                <ul>
                                    {(students || []).map((student) => (
                                        <li key={student._id} style={{ display: "flex", alignItems: "center", justifyContent: "space-between", padding: "5px 0" }}>
                                        <span>{student.name} ({student.email})</span>
                                        <button 
                                            onClick={() => removeStudent(student.email)} 
                                            style={{
                                            backgroundColor: "red",
                                            color: "white",
                                            border: "none",
                                            borderRadius: "50%",
                                            cursor: "pointer",
                                            width: "20px",
                                            height: "20px",
                                            display: "flex",
                                            alignItems: "center",
                                            justifyContent: "center",
                                            marginLeft: "10px"
                                            }}
                                            aria-label="Remove student"
                                        >
                                            &times;
                                        </button>
                                        </li>
                                    ))}
                                    </ul>

                                <button className="add-student-btn" onClick={openModal}>
                                    + Add Student
                                </button>
                            </div>

                            {isModalOpen && (
                                <div className="modal">
                                    <div className="modal-content">
                                        <span className="close-modal" onClick={closeModal}>
                                            &times;
                                        </span>
                                        <h3>Add a Student</h3>
                                        <input
                                            ref={newStudentInput}
                                            type="text"
                                            placeholder="Enter student email"
                                            className="student-input"
                                        />
                                        <button
                                            className="submit-btn"
                                            onClick={() => addStudent(newStudentInput.current.value)}
                                        >
                                            Add
                                        </button>
                                    </div>
                                </div>
                            )}
                        </>
                    )}

                    <button id="log-out" onClick={handleLogout}>
                        Log out
                    </button>
                </div>
            ) : (
                <p>Loading...</p>
            )}
        </>
    );
}
