import "./nav.css";
import closeIcon from "./images/navIcons/closeIcon.png"
import profileIcon from "./images/navIcons/profileIcon.png"
import homeIcon from "./images/navIcons/homeIcon.png"
import calendarIcon from "./images/navIcons/calendarIcon.png"
import resourcesIcon from "./images/navIcons/resourcesIcon.png"
import videoIcon from "./images/navIcons/videoIcon.png"
import notesIcon from "./images/navIcons/notesIcon.png"
import messagesIcon from "./images/navIcons/messagesIcon.png"
import recordingsIcon from "./images/navIcons/recordingsIcon.png"
import NavButton from "./NavButton";
import {useLocation} from "react-router-dom";

export const navLinks= [
    {
        label: "Profile",
        to: "/profile",
        icon: profileIcon
    },
    {
        label: "Home",
        to: "/",
        icon: homeIcon
    },
    {
        label: "Calendar",
        to: "/calendar",
        icon: calendarIcon
    },
    {
        label: "Resources",
        to: "/resources",
        icon: resourcesIcon
    },
    {
        label: "VideoCall",
        to: "/videocall",
        icon: videoIcon
    },
    {
        label: "Messages",
        to: "/messages",
        icon: messagesIcon
    },
    {
        label: "Notes",
        to: "/notes",
        icon: notesIcon
    },
    {
        label: "Recordings",
        to: "/recordings",
        icon: recordingsIcon
    },
];

export default function Nav(props) {

    const location = useLocation()

    return (
        <div className="nav">
            {props.showCloseButton ?
                <NavButton
                    onClick={props.clickCloseButton}
                    icon={closeIcon}
                    text="Close"
                /> : null
            }
            {
                navLinks.map((buttonObj, i) =>
                    <NavButton
                        key={i}
                        to={buttonObj.to}
                        icon={buttonObj.icon}
                        text={buttonObj.label}
                        selected={"/" + location.pathname.split("/")[1] === buttonObj.to}
                    />
                )
            }
        </div>
    );
}
