import React, { useState, useEffect } from 'react'; // Add this line
import "./app.css";
import Header from "./Header";
import Content from "./Content";
import Footer from "./Footer";
import { BrowserRouter, Outlet, Route, Routes } from "react-router-dom";
import HomePage from "./HomePage";
import ProfilePage from "./ProfilePage";
import LoginPage from "./LoginPage";
import CalendarPage from "./CalendarPage";
import VideoCallPage from "./VideoCallPage";
import NotesPage from "./NotesPage";
import RegisterPage from "./RegisterPage";
import GoogleRoleSelect from "./GoogleRoleSelect";
import ResourcesPage from './ResourcesPage';


// Custom hook for handling query state
export function useQueryState(query, onChange = null) {
    const [queryMatch, setQueryMatch] = useState(matchMedia(query).matches); // Fixing the undefined error for useState

    useEffect(() => {
        const widthCheck = matchMedia(query);
        const change = () => {
            setQueryMatch(widthCheck.matches);
            if (onChange) {
                onChange(widthCheck.matches);
            }
        };
        widthCheck.addEventListener("change", change);

        return () => {
            widthCheck.removeEventListener("change", change);
        };
    }, [query, queryMatch, onChange]);  // Fixing the undefined error for useEffect

    return queryMatch;
}

export default function App() {

    return (
        <BrowserRouter>
            <Routes>
                {/* Define the main layout structure */}
                <Route path="/" element={
                    <div className="grid-layout">
                        <Header />
                        <Content>
                            <Outlet />
                        </Content>
                        <Footer />
                    </div>
                }>
                    {/* Home Route */}
                    <Route path="" element={<HomePage />} />

                    {/* Profile Route */}
                    <Route path="profile" element={<ProfilePage />} />

                    {/* Login Route */}
                    <Route path="login" element={<LoginPage />} />

                    {/* Register Route */}
                    <Route path="register" element={<RegisterPage />} />

                    <Route path="googleRegister" element={<GoogleRoleSelect/>} />

                    {/* Calendar Route */}
                    <Route path="calendar" element={<CalendarPage />} />

                    {/* Resources Route */}
                    <Route path="/resources" element={<ResourcesPage />} />

                    {/* VideoCall Route */}
                    <Route path="videocall" element={<VideoCallPage />} />

                    {/* Notes Route */}
                    <Route path="notes" element={<NotesPage />} />

                    {/* Test Route for additional development */}
                    <Route path="test" element={<div>test</div>} />

                    {/* Fallback Route for 404 Not Found */}
                    <Route path="*" element={
                        <>
                            <h1>404</h1>
                            <p>Page not found</p>
                        </>
                    } />
                </Route>
            </Routes>
        </BrowserRouter>
    );
}
