import "./googleRoleSelect.css"
import {useEffect, useState} from "react"
import {useNavigate} from 'react-router-dom'
import studentIcon from "./images/309036_student_education_study_icon.png"
import teacherIcon from "./images/3289566_assistant_educator_professor_showing_teacher_icon.png"
import parentIcon from "./images/3289560_babies_baby_family_kid_people_icon.png"
import {deleteCookie, getCookie, isCookieSet, setCookie} from "./cookieUtil";

export default function GoogleRoleSelect() {

    const [errorMessage, setErrorMessage] = useState("")

    const navigate = useNavigate();

    useEffect(() => {
        if (!isCookieSet("credential")) {
            navigate("/login") // credential expired or doesn't exist, redirect to login
        }
    }, [navigate]);

    const selectRole = (role) => {
        if (!isCookieSet("credential")) {
            navigate("/login") // credential expired or doesn't exist, redirect to login
        }

        fetch("http://localhost:3001/google-register", {
            method: "POST",
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                token: getCookie("credential"),
                role: role
            })
        }).then(response => {
            if (!response.ok) {
                throw new Error("Error connecting to server")
            }

            response.json().then(data => {
                const expiration = 1
                setCookie("token", data.token, expiration)
                setCookie("studentId", data.studentId, expiration)
                setCookie("teacherId", data.teacherId, expiration)
                deleteCookie("credential")
                navigate("/calendar")
            }).catch(error => {
                setErrorMessage(error.message)
            })
        }).catch(error => {
            setErrorMessage(error.message)
        })
    }

    return (
        <div className="google-role-select">
            <p>You haven't logged in with this Google account before. Please select the role you want to use for your account:</p>
            <div className="role-selection">
                <div className="role-option" onClick={() => selectRole("teacher")}>
                    <div className="img-container">
                        <img alt={"Teacher"} src={teacherIcon}/>
                    </div>
                    Teacher
                </div>
                <div className="role-option" onClick={() => selectRole("student")}>
                    <div className="img-container">
                        <img alt={"Student"} src={studentIcon}/>
                    </div>
                    Student
                </div>
                <div className="role-option" onClick={() => selectRole("parent")}>
                    <div className="img-container">
                        <img alt={"Parent"} src={parentIcon}/>
                    </div>
                    Parent
                </div>
            </div>
        </div>
    );
}
