import "./registerPage.css";
import {useRef, useState} from "react";
import Logo from "./Logo";
import {useNavigate} from 'react-router-dom'
import {setCookie} from "./cookieUtil";

export default function RegisterPage() {

    const [errorMessage, setErrorMessage] = useState("")

    const navigate = useNavigate();

    const name = useRef()
    const uname = useRef()
    const pwd = useRef()
    const pwd_confirm = useRef()
    const role = useRef()

    const register = () => {
        if (pwd.current.value !== pwd_confirm.current.value) {
            setErrorMessage("Passwords do not match, please try again.")
            return
        }

        fetch("http://localhost:3001/register", {
            method: "POST",
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                name: name.current.value,
                uname: uname.current.value,
                pwd: pwd.current.value,
                role: (role.current.value)
            })
        }).then(response => {
            if (!response.ok) {
                throw new Error("Error connecting to server")
            }

            response.json().then(data => {
                const expiration = 1;
                setCookie("token", data.token, expiration);
                setCookie("studentId", data.studentId, expiration);
                setCookie("teacherId", data.teacherId, expiration);

                console.log("teacherId 3: ", data.teacherId)

                navigate("/calendar")
            }).catch(error => {
                setErrorMessage(error.message)
            })
        }).catch(error => {
            setErrorMessage(error.message)
        })
    }

    return (
        // <form className="login-form" action="http://localhost:3001/login" method="post">
        <div className="login-form">
            <Logo/>
            <div className="container">
                <label htmlFor="name"><b>Name</b></label>
                <input ref={name} type="text" placeholder="Enter name" name="name" required/>

                <label htmlFor="uname"><b>Username</b></label>
                <input ref={uname} type="email" placeholder="Enter email address" name="uname" required/>

                <label htmlFor="psw"><b>Password</b></label>
                <input ref={pwd} type="password" placeholder="Enter password" name="psw" required/>

                <label htmlFor="psw_confirm"><b>Confirm Password</b></label>
                <input ref={pwd_confirm} type="password" placeholder="Confirm password" name="psw_confirm" required/>

                <label htmlFor="roles">Role: </label>

                <select defaultValue={"student"} ref={role} name="roles" id="roles">
                    <option value="student">Student</option>
                    <option value="teacher">Teacher</option>
                    <option value="parent">Parent</option>
                </select>

                <button onClick={register}>Register</button>

                <p style={{color: "darkred"}}>{errorMessage}</p>

            </div>

            <div className="container">
                <button
                    type="button"
                    className="cancelbtn"
                    onClick={() => {
                        window.history.back()
                    }}
                >Cancel</button>
            </div>
        </div>
    );
}
