import React, { useEffect, useState } from 'react';
import { getCookie } from './cookieUtil.js';
import { getRole } from './authUtil.js';
import './homePage.css';

export default function HomePage() {
    const [upcomingLessons, setUpcomingLessons] = useState([]);
    const [role, setRole] = useState(null);

    // Helper function to fetch student names based on IDs
    const fetchStudentNames = async (studentIds) => {
        const studentNames = await Promise.all(studentIds.map(async (id) => {
            try {
                const response = await fetch(`http://localhost:3001/students/${id}`);
                if (!response.ok) {
                    console.error(`Failed to fetch student with ID ${id}: ${response.status}`);
                    return 'Unknown'; // Fallback to 'Unknown' if not found
                }
                const student = await response.json();
                return student?.name || 'Unknown';
            } catch (error) {
                console.error(`Error fetching student with ID ${id}:`, error);
                return 'Unknown';
            }
        }));
        return studentNames;
    };

    // Helper function to fetch the teacher's name based on ID
    const fetchTeacherName = async (teacherId) => {
        try {
            const response = await fetch(`http://localhost:3001/teachers/${teacherId}`);
            if (!response.ok) {
                console.error(`Failed to fetch teacher with ID ${teacherId}: ${response.status}`);
                return 'Unknown';
            }
            const teacher = await response.json();
            return teacher?.name || 'Unknown';
        } catch (error) {
            console.error(`Error fetching teacher with ID ${teacherId}:`, error);
            return 'Unknown';
        }
    };

    // Function to fetch upcoming lessons
    const fetchUpcomingLessons = async () => {
        const studentId = getCookie("studentId");
        const teacherId = getCookie("teacherId");

        try {
            const userRole = await getRole();
            setRole(userRole);

            const userId = userRole === "teacher" ? teacherId : studentId;
            const roleKey = userRole === "teacher" ? "teacherId" : "studentId";
            const response = await fetch(`http://localhost:3001/lessons/upcoming?${roleKey}=${userId}`);

            if (response.ok) {
                const data = await response.json();

                // Fetch student names and teacher names for each lesson
                const lessonsWithNames = await Promise.all(data.map(async (lesson) => {
                    const studentNames = await fetchStudentNames(lesson.studentIds || []);
                    const teacherName = await fetchTeacherName(lesson.teacherIds?.[0] || '');
                    return {
                        ...lesson,
                        studentNames: studentNames || [], // Initialize as an empty array
                        teacherName: teacherName || 'Unknown', // Default to 'Unknown' if not found
                    };
                }));

                setUpcomingLessons(lessonsWithNames);
            } else {
                console.error("Failed to fetch upcoming lessons:", response.statusText);
            }
        } catch (error) {
            console.error("Error fetching upcoming lessons:", error);
        }
    };

    useEffect(() => {
        fetchUpcomingLessons();
    }, []);

    return (
        <div className="home-container">
            <h1 className="title">Upcoming Lessons</h1>
            {upcomingLessons.length > 0 ? (
                <div className="lesson-list">
                    {upcomingLessons.map((lesson) => (
                        <div className="lesson-card" key={lesson._id}>
                            <div className="lesson-card-header">
                                <h2>{lesson.title || "Lesson"}</h2>
                                <span>{new Date(lesson.date).toLocaleString()}</span>
                            </div>
                            <div className="lesson-card-body">
                                <p><strong>Duration:</strong> {lesson.duration} minutes</p>
                                <p><strong>Notes:</strong> {lesson.notes || "No notes provided"}</p>

                                {/* Show students for teacher view */}
                                {role === "teacher" && (
                                    <p>
                                        <strong>Students:</strong> 
                                        {(lesson.studentNames && lesson.studentNames.length > 0) 
                                            ? lesson.studentNames.join(', ') 
                                            : "No students listed"}
                                    </p>
                                )}

                                {/* Show teacher for student view */}
                                {role === "student" && (
                                    <p>
                                        <strong>Teacher: </strong> 
                                        {lesson.teacherName || "Unknown"}
                                    </p>
                                )}
                            </div>
                        </div>
                    ))}
                </div>
            ) : (
                <p className="no-lessons">No upcoming lessons</p>
            )}
        </div>
    );
}
