import React, { useRef, useState, useEffect } from 'react';
import './videoCallPage.css';

const signalingServerUrl = 'ws://localhost:8080'; // WebSocket server URL

export default function VideoCallPage() {
    const localVideoRef = useRef(null);
    const remoteVideoRef = useRef(null);
    const [inCall, setInCall] = useState(false);
    const [userEmail, setUserEmail] = useState("");
    const [wsConnection, setWsConnection] = useState(null);
    const [peerConnection, setPeerConnection] = useState(null);

    // WebRTC setup
    const mediaConstraints = { video: true, audio: true }; // Adjust as needed

    useEffect(() => {
        // Initialize WebSocket connection to signaling server
        const socket = new WebSocket(signalingServerUrl);

        socket.onopen = () => {
            console.log("Connected to signaling server");
        };

        socket.onmessage = (message) => {
            const signal = JSON.parse(message.data);
            handleSignalingMessage(signal);
        };

        socket.onclose = () => {
            console.log("Disconnected from signaling server");
        };

        setWsConnection(socket);

        return () => {
            socket.close();
        };
    // eslint-disable-next-line
    }, []);

    const handleSignalingMessage = (signal) => {
        // eslint-disable-next-line
        const pc = peerConnection;

        switch (signal.type) {
            case 'offer':
                handleOffer(signal.offer);
                break;
            case 'answer':
                handleAnswer(signal.answer);
                break;
            case 'ice-candidate':
                handleICECandidate(signal.candidate);
                break;
            default:
                break;
        }
    };

    // Get media from user device
    const getMedia = async () => {
        try {
            const stream = await navigator.mediaDevices.getUserMedia(mediaConstraints);
            localVideoRef.current.srcObject = stream;
            return stream;
        } catch (error) {
            console.error('Error accessing media devices.', error);
        }
    };

    const startCall = async () => {
        if (!userEmail) {
            alert("Please enter an email to start the call");
            return;
        }

        setInCall(true);

        // Get local media stream
        const stream = await getMedia();
        
        // Create a new peer connection
        const pc = new RTCPeerConnection({
            iceServers: [{ urls: 'stun:stun.l.google.com:19302' }] // Google public STUN server
        });

        // Add local stream to the peer connection
        stream.getTracks().forEach(track => pc.addTrack(track, stream));

        // Handle remote stream when it's received
        pc.ontrack = (event) => {
            remoteVideoRef.current.srcObject = event.streams[0];
        };

        // Handle ICE candidates
        pc.onicecandidate = (event) => {
            if (event.candidate) {
                sendSignal({ type: 'ice-candidate', candidate: event.candidate });
            }
        };

        // Create and send offer to remote peer
        const offer = await pc.createOffer();
        await pc.setLocalDescription(offer);

        // Send the offer to the remote peer via WebSocket
        sendSignal({ type: 'offer', offer });

        setPeerConnection(pc);
    };

    const handleOffer = async (offer) => {
        const pc = new RTCPeerConnection({
            iceServers: [{ urls: 'stun:stun.l.google.com:19302' }] // Google public STUN server
        });

        // Set remote description (offer)
        await pc.setRemoteDescription(new RTCSessionDescription(offer));

        // Get local media stream and add to peer connection
        const stream = await getMedia();
        stream.getTracks().forEach(track => pc.addTrack(track, stream));

        // Handle remote stream when it's received
        pc.ontrack = (event) => {
            remoteVideoRef.current.srcObject = event.streams[0];
        };

        // Handle ICE candidates
        pc.onicecandidate = (event) => {
            if (event.candidate) {
                sendSignal({ type: 'ice-candidate', candidate: event.candidate });
            }
        };

        // Create and send answer back to remote peer
        const answer = await pc.createAnswer();
        await pc.setLocalDescription(answer);
        sendSignal({ type: 'answer', answer });

        setPeerConnection(pc);
    };

    const handleAnswer = async (answer) => {
        const pc = peerConnection;
        await pc.setRemoteDescription(new RTCSessionDescription(answer));
    };

    const handleICECandidate = (candidate) => {
        const pc = peerConnection;
        pc.addIceCandidate(new RTCIceCandidate(candidate));
    };

    const sendSignal = (signal) => {
        if (wsConnection && wsConnection.readyState === WebSocket.OPEN) {
            wsConnection.send(JSON.stringify(signal));
        }
    };

    const endCall = () => {
        if (peerConnection) {
            peerConnection.close();
            setPeerConnection(null);
        }
        setInCall(false);
        setUserEmail('');
    };

    return (
        <div className="video-call-container">
            {/* Email input */}
            <div className="email-input">
                <input
                    type="text"
                    placeholder="Enter email to call"
                    value={userEmail}
                    onChange={(e) => setUserEmail(e.target.value)}
                    disabled={inCall}
                />
            </div>

            {/* Video elements */}
            <div className="video-wrapper">
                <video ref={localVideoRef} autoPlay muted className="local-video"></video>
                <video ref={remoteVideoRef} autoPlay className="remote-video"></video>
            </div>

            {/* Control Buttons */}
            <div className="controls">
                {!inCall ? (
                    <button onClick={startCall}>Start Call</button>
                ) : (
                    <button onClick={endCall}>End Call</button>
                )}
            </div>
        </div>
    );
}
