import {getCookie} from "./cookieUtil";

/**
 * get the role of the logged in user
 * @returns {Promise<undefined|*>} promise returning the role as string, or undefined if the user is not logged in
 */
export async function getRole() {
    const response = await fetch(
        `http://localhost:3001/user/role?token=${getCookie("token")}`
    )
    if (response.status === 401) {
        return undefined
    }

    const json = await response.json()
    return json.role
}